<template>
  <div id="header" class="header-container">
    {{text}}{{text1}}
    ceshi
  </div>
</template>

<script>
    export default {
      name: "header-com",
      data () {
        return {
          text: '',
          text1: ''
        }
      },
      created () {
        this.$nextTick(() => {
          this.text1 = $api.trimAll('  abc 123  ')
          this.text = $api.dom('div', '#header')
          console.log($api.dom('div', '#header'))
        })
      }
    }
</script>

<style scoped>

</style>



